<style scoped>

	.v-data-table >>> .v-data-table-header th {
		text-align: center !important;
	}
	.cursortext >>> input{
		cursor:pointer;
	}
    #loading_img{
        position: relative;
        top:22%;
        left:25%;
    }
	.v-data-footer{
		zoom:70%;
	}
	.income_date >>> input{
		text-align: start !important;
		padding-left: 0px;
		padding-right: 0px;
	}
</style>
<style>
	.swal2-title{
		font-size:22px !important;
	}
	.v-data-footer__pagination{
		margin: 0px !important;
	}

</style>
<template>
	<v-layout wrap row>
		<v-flex xs12 sm12 md12 style="margin-bottom:20px;">
			<v-card xs12 sm12 md12>
				<v-card-title xs12 sm12 md12 class="fontDefault">
					<!-- {{ this.$router.app.$route.query.screen_name }} -->
					자재 입고 관리
				</v-card-title>
			</v-card>
		</v-flex>
		<v-flex xs12 sm12 md12>
			<v-card>
				<v-card-text>
					<template v-if="this.save_yn=='N'">
						<v-row>
							<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-bottom:0px;margin-bottom:0px;">
								<v-text-field v-model="schVal" class="fontDefault" label="검색" single-line @keypress.enter.prevent="getList"></v-text-field>
							</v-col>
						</v-row>
						<v-row class="justify-end">
							<template v-if="this.$router.app.$route.query.update_yn=='Y'">
								<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
									<v-btn width="100%" color="green" v-on:click="Regopen" style="color:white;">등록</v-btn>
								</v-col>
							</template>
							<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="white" v-on:click="getList">검색</v-btn>
							</v-col>
						</v-row>
					</template>
					<template v-else>
						<v-row>
							<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-bottom:0px;margin-bottom:0px;">
								<v-text-field v-model="schVal" class="fontDefault" label="검색" single-line disabled></v-text-field>
							</v-col>
						</v-row>
						<v-row class="justify-end">
							<template v-if="this.$router.app.$route.query.update_yn=='Y'">
								<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
									<v-btn width="100%" color="orange" disabled style="color:white;">등록</v-btn>
								</v-col>
							</template>
							<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="primary" disabled>검색</v-btn>
							</v-col>
						</v-row>
					</template>
					<v-row>
						<template v-if="this.save_yn=='N'">
							<v-col>
								<v-data-table mobile-breakpoint class="elevation-1" :footer-props="{ 'items-per-page-text': '페이지 당 행',  pageText: '총 {2} 항목 중 {0}-{1}'}"  @click:row="onClickRow" :headers="headers" :items="document" @pagination="updatePage"  :itemsPerPage="5" :loading="loading" >
									<template slot="items" slot-scope="props">
										<td>{{ props.item.num }}</td>
										<td>{{ props.item.clm_employee_number }}</td>
										<td>{{ props.item.clm_user_id }}</td>
										<td>{{ props.item.clm_user_name }}</td>
										<td>{{ props.item.clm_user_department_id }}</td>
										<td>{{ props.item.clm_user_authority_id }}</td>
										<td>{{ props.item.clm_comment }}</td>
									</template>
								</v-data-table>
							</v-col>
						</template>
						<template v-else>
							<img style="padding-top:100px;padding-bottom:100px;" id="loading_img" src="product/loading.gif" />
						</template>
					</v-row>
					<v-row justify="center">
						<v-dialog v-model="regmodal" persistent max-width="950" @keydown.esc="regmodal=false">
							<v-card>
							<v-toolbar class="fontDefault" color="light-blue" dark>자재 입고 등록</v-toolbar>
							<v-card-text style="padding-top:15px;">
								<template v-if="this.clm_material_income_id==''">
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:20px; padding-bottom:0px;">
											<span style="color:red; float: left; padding-top:20px;">*</span><v-text-field label="거래처" @click="getClientModal" v-model="clm_material_income_client_name" readonly class="cursortext"/>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="6" md="6" sm="6" cols="6" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<span style="color:red; float: left; padding-top:20px;">*</span><v-text-field class="income_date" type="date" label="입고일자" v-model="clm_material_income_date" />
										</v-col>
										<v-col xl="6" md="6" sm="6" cols="6" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-text-field label="입고수량" v-model="clm_material_income_count" type="number" />
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-select
												v-model="clm_material_type" 
												:items="clm_material_type_items" 
												item-text="clm_code_sub_name"
												item-value="clm_code_sub_id"
												v-on:change="ChangeType"
												open-on-clear
												label="자재분류">
											</v-select>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-select
												v-model="clm_material_name" 
												:items="clm_material_name_items" 
												item-text="clm_code_sub_name"
												item-value="clm_code_sub_id"
												v-on:change="ChangeName"
												open-on-clear
												label="자재명">
											</v-select>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-select
												v-model="clm_material_spec" 
												:items="clm_material_spec_items" 
												item-text="clm_code_sub_name"
												item-value="clm_code_sub_id"
												open-on-clear
												label="자재규격">
											</v-select>
										</v-col>
									</v-row>
								</template>
								<template v-else>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:20px; padding-bottom:0px;">
											<v-text-field label="자재입고번호" v-model="clm_material_income_id" disabled />
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-text-field label="거래처" @click="getClientModal" v-model="clm_material_income_client_name" disabled/>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="6" md="6" sm="6" cols="6" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-text-field label="입고일자" v-model="clm_material_income_date" disabled/>
										</v-col>
										<v-col xl="6" md="6" sm="6" cols="6" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-text-field label="입고수량" v-model="clm_material_income_count" type="number" disabled/>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-select
												v-model="clm_material_type" 
												:items="clm_material_type_items" 
												item-text="clm_code_sub_name"
												item-value="clm_code_sub_id"
												disabled
												open-on-clear
												label="자재분류">
											</v-select>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-select
												v-model="clm_material_name" 
												:items="clm_material_name_items" 
												item-text="clm_code_sub_name"
												item-value="clm_code_sub_id"
												disabled
												open-on-clear
												label="자재명">
											</v-select>
										</v-col>
									</v-row>
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="padding-top:0px; padding-bottom:0px;">
											<v-select
												v-model="clm_material_spec" 
												:items="clm_material_spec_items" 
												item-text="clm_code_sub_name"
												item-value="clm_code_sub_id"
												open-on-clear
												disabled
												label="자재규격">
											</v-select>
										</v-col>
									</v-row>
								</template>
							</v-card-text>
							<v-card-actions class="justify-end">
								<template v-if="this.clm_material_income_id=='' && this.$router.app.$route.query.update_yn=='Y'">
								<v-btn text @click="Save" >등록</v-btn>
								</template>
								<v-btn text @click="regmodal = !regmodal">닫기</v-btn>
							</v-card-actions>
							</v-card>
						</v-dialog>
					</v-row>		
					<v-row justify="center">
						<v-dialog v-model="clientModal" persistent max-width="950" @keydown.esc="clientModal=false">
							<v-card>
							<v-toolbar class="fontDefault" color="light-blue" dark>거래처 정보</v-toolbar>
							<v-card-text style="padding-top:15px;">
								<v-row>
									<v-col xl="9" md="9" sm="9" cols="9" align-self="center"><!--모바일에서 깨져보여서 -->
										<v-text-field label="거래처명" v-model="clm_m_s_client_name" @keypress.enter.prevent="getclientModalList" />
									</v-col>
									<v-col xl="2" md="2" sm="2" cols="2" align-self="center">
										<v-btn width="100%" color="primary" v-on:click="getclientModalList">검색</v-btn>
									</v-col>
								</v-row>
								<v-row style="padding-left:17px;">
									<v-data-table no-data-text="조회된 거래처가 없습니다."  :footer-props="{ 'items-per-page-text': '페이지 당 행',  pageText: '총 {2} 항목 중 {0}-{1}'}"  mobile-breakpoint="0" class="elevation-1" :headers="client_headers" :items="client_document" @click:row="onClickClientModalRow" :itemsPerPage="5" :loading="loading" style="width:95%;" >
										<template slot="items" slot-scope="props">
											<td>{{ props.item.num }}</td>
											<td>{{ props.item.clm_client_name }}</td>
											<td>{{ props.item.clm_client_tel }}</td>
										</template>
									</v-data-table>
								</v-row>
							</v-card-text>
							<v-card-actions class="justify-end">
								<v-btn text @click="clientModal=false">이전</v-btn>
							</v-card-actions>
							</v-card>
						</v-dialog>
					</v-row>	
				</v-card-text>
			</v-card>
		</v-flex>
	</v-layout>
</template>

<script src="//cdn.jsdelivr.net/npm/sweetalert2@11"></script>
<script>
import axios from 'axios';
import Swal from 'sweetalert2'

export default {
	data() {
		return {
			headers: [
				// { text: '순번', align: 'end', value: 'num', width: 80 },
				{ text: '입고번호', align: 'center', value: 'clm_material_income_id', width: 180 },
				{ text: '거래처명', align: 'start', value: 'clm_material_income_client_name', width: 210 },
				{ text: '입고일자', align: 'center', value: 'clm_material_income_date', width: 170 },
				{ text: '입고수량', align: 'end', value: 'clm_material_income_count', width: 130 },
				{ text: '자재명', align: 'start', value: 'clm_material_name', width: 280 },
			],
			document: [],
			options: {
				multiSort: false,
				sortBy: [],
				sortDesc: [],
				page: 1,
				itemsPerPage: 5,
			},
			footerOptions: {
				'items-per-page-options': [5, 10, 25, 50, 100],
			},
			totalCount: 0,
			loading: false,
			regmodal:false,
			schVal: '',
			save_yn:'N',
			list_page:10,
			loading_yn:'N',
			schVal2:'N',
			/* 자재입고 모달 */
			clm_material_income_id:'',
			clm_material_income_client_id :'',
			clm_material_income_client_name:'',
			clm_material_income_date :'',
			clm_material_income_count :'',
			clm_material_id:'',
			clm_material_name:'',
			clm_material_spec:'',
			clm_material_type:'',
			clm_material_type_items:[],
			clm_material_name_items:[],
			clm_material_spec_items:[],
			/* 거래처 모달 */
			clientModal :false,
			clm_m_s_client_name:'',
			client_document: [],
			client_headers: [
				{ text: '순번', align: ' d-none', value: 'num', width: 80 },
				{ text: '고객ID', align: ' d-none', value: 'clm_client_id'  },
				{ text: '거래처명', align: 'start', value: 'clm_client_name', width: 200 },
				{ text: '사업자번호', align: 'center', value: 'clm_client_serial_no', width: 200 },
				{ text: '대표자', align: 'center', value: 'clm_client_ceo', width: 150 }
			],
		}
	},
	mounted() {
		axios.get('/back/Popup/MaterialTypeList')
		.then(response => {
			this.clm_material_type_items = response.data;
		})
		.catch(error => {
			console.log(error)
		})
		var cart = {};
		cart.clm_code_sub_id = '';
		cart.clm_code_sub_name = '선택';
		this.clm_material_type_items.unshift(cart);
		this.getList()
	},
	watch: {
		options: {
			handler() {
				this.getList()
			},
			deep: true,
		},
	},
	methods: {
		updatePage(page){
			this.list_page=Number(page.itemsPerPage);
		}, 
		Regopen(){
			this.loading_yn='N';
			
			var today = new Date();
			var year = today.getFullYear();
			var month = ('0' + (today.getMonth() + 1)).slice(-2);
			var day = ('0' + today.getDate()).slice(-2);
			var dateString = year + '-' + month  + '-' + day;
			this.clm_material_type='';
			this.clm_material_income_id='';
			this.clm_material_income_client_id='';
			this.clm_material_income_client_name='';
			this.clm_material_id='';
			this.clm_material_name='';
			this.clm_material_spec='';
			this.clm_material_income_date=dateString;
			this.clm_material_income_count =0;
			this.clm_material_name_items =[];
			this.clm_material_spec_items =[];
			this.regmodal=true;
		},
		Save() {
			if(this.loading_yn=='Y'){
				Swal.fire({
					title: '저장중입니다.잠시만 기다려주세요.',
					icon: 'error'
				});
				return;
			}
			this.loading_yn='Y';
			if(this.clm_material_income_date == ''){
				Swal.fire({
					title: '입고일자를 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_material_income_client_id == ''){
				Swal.fire({
					title: '거래처를 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;			
			}else if(this.clm_material_type == ''){
				Swal.fire({
					title: '자재분류를 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_material_name == ''){
				Swal.fire({
					title: '자재명을 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_material_spec == ''){
				Swal.fire({
					title: '자재규격을 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_material_income_count=='' || Number(this.clm_material_income_count)<=0 || Number(this.clm_material_income_count)%1 !=0){
				Swal.fire({
					title: '입고수량이 정상적이지 않습니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else{
				axios.post('/back/SCR001001/Reg', null, { params: {
					clm_material_income_date: this.clm_material_income_date,
					clm_material_income_client_id: this.clm_material_income_client_id,
					clm_material_id: this.clm_material_spec,
					clm_material_income_count: this.clm_material_income_count,
					clm_user: this.$session.get('user_id')
				}})
				.then(response => {
					if (response.status == '200') {
						this.loading_yn='N';
						Swal.fire({
							title: '정상적으로 등록 되었습니다.',
							icon: 'success'
						});
					}else{
						this.loading_yn='N';
					}
					this.regmodal = false;
					this.getList();
				})
				.catch(error => {
					console.log(error)
				})		
			}
			
		},
		getClientModal() {
			this.clm_m_s_client_name='';
			this.clientModal = true;
			this.getclientModalList();
		},
		getclientModalList() {
			let items = axios.get('/back/Popup/ClientList',{
				params: {
			          search: this.clm_m_s_client_name,
					  clm_company_key : this.$session.get('company_key'),
					}
				}).then(
				response => {
					items = response.data
					setTimeout(() => {
						this.client_document = items
					}, 500)
				},
			)
		},
		onClickClientModalRow(event, data) {
			this.clm_material_income_client_name = data.item.clm_client_name,
			this.clm_material_income_client_id = data.item.clm_client_id,
			this.clientModal = false
		},
		ChangeType(){
			this.clm_material_name='';
			this.clm_material_spec='';
			axios.get('/back/Popup/MaterialNameList', {
				params: {
					clm_material_type_id :this.clm_material_type,
				}
			})
			.then(response => {
			console.log(this.clm_material_type)
				this.clm_material_name_items = response.data;
				// this.clm_material_name_items.unshift({
				// 	clm_code_sub_name: '선택',
				// 	clm_code_sub_id: ''
				// })
			})
			.catch(error => {
				console.log(error)
			})
		},
		ChangeName(){
			this.clm_material_spec='';
			axios.get('/back/Popup/MaterialSpecList', {
				params: {
					clm_material_type_id :this.clm_material_type,
					clm_material_name_id :this.clm_material_name,
				}
			})
			.then(response => {
				this.clm_material_spec_items = response.data;
				// this.clm_material_spec_items.unshift({
				// 	clm_code_sub_name: '선택',
				// 	clm_code_sub_id: ''
				// })
			})
			.catch(error => {
				console.log(error)
			})
		},
		getList() {
			const vm = this
			this.loading = true
			this.save_yn='Y';
			
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let sort = []
				let TsortBy = ''
				let TsortDesc = ''
				if (sortBy.length > 0) {
					// eslint-disable-next-line
					sortBy.forEach((value, index) => {
						TsortBy = value
						if (sortDesc[index]){
							TsortDesc = 'desc'
						}else{
							TsortDesc = 'asc'
						}
					})
				} else {
					TsortBy = ''
					TsortDesc = ''
					
				}
				let items = axios.get('/back/SCR001001/List',{
						params: {
						sortBy: TsortBy,
						sortDesc: TsortDesc,
						search: this.schVal,
						page: page,
						PerPage: itemsPerPage,
						clm_company_key : this.$session.get('company_key'),
				        }
					}).then(
					response => {
						items = response.data
						setTimeout(() => {
							vm.loading = false
							this.save_yn='N';
							this.document = items
						}, 1000)
					},
				)
			})
		},
		onClickRow(event, data){
			this.clm_material_income_id = data.item.clm_material_income_id;
			this.clm_material_income_client_id = data.item.clm_material_income_client_id;
			this.clm_material_income_client_name = data.item.clm_material_income_client_name;
			this.clm_material_income_date  = data.item.clm_material_income_date;
			this.clm_material_income_count  = Number(String(data.item.clm_material_income_count).replace(/,/gi, ""));
			this.clm_material_id = data.item.clm_material_id;
			this.clm_material_type = data.item.material_type;
			this.ChangeType();
			this.clm_material_name = data.item.material_name;
			this.ChangeName();
			this.clm_material_spec = data.item.clm_material_id;
			this.regmodal=true;
		},
	},
}
</script>